import Todo from "../components/todo";
import { getTodos } from "@/actions/todo.action";

export default async function historyPage(){
    const todos = await getTodos();
    console.log(todos);

    return (
        <div>
            <h1>View your saved quizzes here</h1>
            <div>
                {todos && todos.map(todo => {
                return (
                    <Todo 
                        key={todo._id}
                        text={todo.message}
                        id={todo._id}
                    />
                );
                })}
            </div>
        </div>
    );
    
}